<template>
  <h3 class="shop__title">附件商家</h3>
  <div class="shop">
    <router-link
      :to="`/product/${item._id}`"
      v-for="item in shopList"
      :key="item._id"
    >
      <div class="shop__item">
        <div class="shop__img">
          <img :src="item.imgUrl" />
        </div>
        <div class="shop__shopInfo">
          <div class="shop__shopInfo__name" :title="item.name">
            {{ item.name }}
          </div>
          <div class="shop__shopInfo__sales">月售{{ item.sales }}</div>
          <div class="shop__shopInfo__text">
            <span class="shop__shopInfo__sendPrice"
              >起送 &yen;{{ item.sendPrice }}</span
            >
            <span class="shop__shopInfo__shippingPrice"
              >配送 &yen;{{ item.shippingPrice }}</span
            >
          </div>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
import { ref } from 'vue'
import { get } from '../../utils/request'

export default {
  name: 'Shop',
  setup() {
    const shopList = ref([])
    const getShopList = async () => {
      const result = await get('/api/shoplist')
      if (result?.errno === 0 && result?.data?.length) {
        shopList.value = result.data
      }
    }
    getShopList()

    return { shopList }
  }
}
</script>

<style lang="scss" scoped>
.shop {
  padding: 0 0.12rem;
  overflow: hidden;
  overflow-y: scroll;
  &__title {
    padding-left: 0.12rem;
    font-size: 0.2rem;
    margin: 0 0 0.16rem 0;
  }
  &__item {
    display: flex;
    border-radius: 0.12rem;
    background-color: #fff;
    padding: 0.12rem;
    margin-bottom: 0.12rem;
  }
  &__img {
    width: 0.8rem;
    height: 0.68rem;
    margin-right: 0.1rem;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  &__shopInfo {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__name {
      width: 2.2rem;
      font-size: 0.14rem;
      font-weight: bold;
      margin-bottom: 0.1rem;
      color: #333;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    &__sales {
      font-size: 0.1rem;
      color: #666;
      margin-bottom: 0.06rem;
    }
    &__text {
      font-size: 0.1rem;
      color: #666;
    }
    &__sendPrice {
      margin-right: 0.1rem;
    }
  }
}
</style>
